import * as React from 'react';

import Checkbox from '$components/UI/Checkbox';
import { optionsUIMenu } from '$constants/menu';
import Modal from '$components/UI/Modal';

const styles = require('./styles.scss');

interface IComponentProps {
  active: boolean,
  scenario: string,
  changeScenario: (type: string) => void,
  onSelectScenario: (event) => void,
  onConfirm: () => void,
  onCloseModal: () => void,
}

const Component: React.FunctionComponent<IComponentProps> = ({
  active,
  scenario,
  changeScenario,
  onSelectScenario,
  onConfirm,
  onCloseModal,
}) => active ? (
  <Modal
    title=""
    confirmText="Add"
    onConfirm={onConfirm}
    cancelText="Close"
    onClose={onCloseModal}
  >
    <div className={styles.modal__body}>
      <div className={styles.section}>
        <h5>Scenario</h5>
        <Checkbox
          name="oneClick"
          label="one click scenario"
          onChange={() => changeScenario('oneClick')}
        />
        <Checkbox
          name="combined"
          label="combined scenario"
          onChange={() => changeScenario('combined')}
        />
        <select onChange={onSelectScenario} value={scenario}>
          <option value="">Scenario type</option>
          {optionsUIMenu.map(item => (
            <option key={item.value} value={item.value}>{item.label}</option>
          ))}
        </select>
      </div>
      <div className={styles.section}>
        <h5>UI element</h5>
        <Checkbox
          name="picture"
          label="picture"
          onChange={() => changeScenario('picture')}
        />
        <Checkbox
          name="text"
          label="text"
          onChange={() => changeScenario('text')}
        />
        <Checkbox
          name="widget"
          label="widget"
          onChange={() => changeScenario('widget')}
        />
        <select>
          <option value="">Widget type</option>
          <option value="maps">Google maps</option>
          <option value="calendar">Calendar</option>
          <option value="pictureDay">Picture of the day</option>
        </select>
      </div>
    </div>
  </Modal>
) : null;

export const ModalAddElement = Component;
