import * as React from 'react';

const styles = require('./styles.scss');

interface ICheckbox {
  name: string,
  label?: string,
  disabled?: boolean,
  onChange: (event) => void,
  checked?: boolean
}

let Ceckbox = (props: ICheckbox) => {
  const {
    name,
    label,
    onChange,
    checked,
  } = props;

  return (
    <label
      className={styles.checkbox}
      htmlFor={name}
    >
      <input
        name={name}
        type="checkbox"
        onChange={onChange}
        checked={checked}
      />
      <span>
        {label}
      </span>
    </label>
  );
};

export default Ceckbox = React.memo(Ceckbox);
