import * as React from 'react';
import Button from '$components/UI/Button';
import { Icon } from '$components/UI/Icon';
import { IActiveItem } from '$components/Editor';
import Checkbox from '$components/UI/Checkbox';

const styles = require('./styles.scss');

interface IComponentProps {
  activeItem: IActiveItem,
  disablePanel: boolean,
  isMoving: boolean,
  addElement: () => void,
  dellElement: () => void,
  onChangeMove: (event) => void,
  onChangeGrid: (event) => void,
}

const Component: React.FunctionComponent<IComponentProps> = ({
  activeItem,
  isMoving,
  disablePanel,
  addElement,
  dellElement,
  onChangeMove,
  onChangeGrid,
}) => {
  return (
    <div className={styles.propsPanel}>
      <div className={styles.navigate}>
        <Checkbox
          name="isGrid"
          label="Grid"
          onChange={onChangeGrid}
        />
        <Checkbox
          name="isMoving"
          label="Moving"
          checked={isMoving}
          onChange={onChangeMove}
        />
      </div>
      <div className={styles.activeElement}>
        <Button
          className={styles.addElement}
          type="none"
          onClick={addElement}
          disabled={disablePanel}
        >
          <Icon icon="addCircle" />
        </Button>
        <p>
          {`select: ${activeItem.itemId}`}
        </p>
        { activeItem.parentId !== '-1' && (
          <Button
            className={styles.delElement}
            type="none"
            onClick={dellElement}
            disabled={disablePanel}
          >
            <Icon icon="delete" />
          </Button>
        )}
      </div>
      <div>
        Panel Edit Props
      </div>
    </div>
  );
};

export const PanelEditProps = Component;
