import * as React from 'react';
import { Prompt } from 'react-router';

import { uniqueId } from '$redux/sagas';
import { PanelEditProps } from '$components/Editor/PanelEditProps';
import Recursive from '$containers/Recursive';
import { Icon } from '$components/UI/Icon';
import Button from '$components/UI/Button';
import Modal from '$components/UI/Modal';
import { ModalAddElement } from '$components/Editor/ModalAddElement';
import { IProjectScreen } from '$redux/project/reducer';
import { IOnChange } from '$components/Editor/SortableGroup';
import { IListSortable, ISortableItem } from '$utils/parseListItems';
import {
  TDelScreen,
  TAddElement,
  TDelElement,
  TSetScreen,
  TSetListSortable,
} from '$redux/project/actions';

const styles = require('./styles.scss');


export interface IActiveItem {
  parentId: string,
  itemId: string,
}

interface IEditorProps {
  projectId: string,
  screen: IProjectScreen,
  delScreen: TDelScreen,
  setScreen: TSetScreen,
  addElement: TAddElement,
  dellElement: TDelElement,
  setListSortable: TSetListSortable,
}

interface IEditorState {
  scenario: string,
  modalAddScreen: boolean,
  modalDellScreen: boolean,
  activeItem: IActiveItem,
  isMoving: boolean,
}


class Editor extends React.Component<IEditorProps, IEditorState> {
  constructor(props) {
    super(props);
    this.state = {
      scenario: '',
      modalAddScreen: false,
      modalDellScreen: false,
      isMoving: true,
      activeItem: {
        parentId: '-1',
        itemId: 'root',
      },
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.screen.id !== this.props.screen.id) {
      this.setState({
        activeItem: {
          parentId: '-1',
          itemId: 'root',
        },
      });
    }
  }

  openModalAdd = () => {
    this.setState({ modalAddScreen: true });
  };

  openModalDell = () => {
    this.setState({
      modalDellScreen: true,
    });
  };

  closeModal = () => {
    this.setState({
      modalAddScreen: false,
      modalDellScreen: false,
    });
  };

  onConfirmModal = () => {
    const {
      props: { addElement },
      state: { scenario, activeItem },
    } = this;

    const id = `${scenario}${uniqueId()}`;

    const newElement: ISortableItem = {
      id,
      type: scenario,
      children: [],
    };

    addElement(activeItem.itemId, newElement);

    this.setState({
      modalAddScreen: false,
    });
  };

  onConfirmModalDell = () => {
    const { delScreen, projectId, screen } = this.props;
    this.setState({ modalDellScreen: false });

    delScreen(projectId, screen.id);
  };

  onSelectScenario = ({ target }) => {
    this.setState({ scenario: target.value });
  };

  onItemActive = (parentId, itemId) => {
    this.setState({ activeItem: { parentId, itemId } });
  };

  handleDellElement = () => {
    const {
      props: { dellElement },
      state: { activeItem },
    } = this;

    if (activeItem.parentId === '-1') return;

    dellElement(activeItem.parentId, activeItem.itemId);

    this.setState({ activeItem: { parentId: '-1', itemId: 'root' } });
  };

  updateSortableList: IOnChange = (parent, items) => {
    const { screen: { listSortable }, setListSortable } = this.props;
    const newLS: IListSortable = {
      ...listSortable,
      [parent]: {
        ...listSortable[parent],
        children: items,
      },
    };

    setListSortable(newLS);
  };

  changeMoveElements = ({ target }) => {
    this.setState({ isMoving: target.checked });
  };

  saveScreen = () => {};

  changeScenario = type => ({ target }) => {
    console.log(type, target.checked);
  };

  render() {
    const {
      props: { screen },
      state: {
        activeItem,
        scenario,
        modalAddScreen,
        modalDellScreen,
        isMoving,
      },
    } = this;

    return (
      <div className={styles.main}>
        <div className={styles.navBar}>
          <div>
            left navPanel
          </div>
          <div className={styles.navBarTypeScreen}>
            <Button type="none" onClick={console.log}>
              <Icon icon="computer" size={32} />
            </Button>
            <Button type="none" onClick={console.log}>
              <Icon icon="table" />
            </Button>
            <Button type="none" onClick={console.log}>
              <Icon icon="phone" />
            </Button>
          </div>
          <div className={styles.navBarRight}>
            <Button
              type="none"
              onClick={this.openModalDell}
              disabled={!screen}
            >
              <Icon icon="delete" size={28} />
            </Button>
            <Button
              type="none"
              onClick={this.saveScreen}
              disabled={!screen}
            >
              <Icon icon="save" size={28} />
            </Button>
          </div>
        </div>
        <div className={styles.contain}>
          <div className={styles.wrapper}>
            {
              screen && (
                <Recursive
                  isMoving={isMoving}
                  item={screen.listSortable.root}
                  styles={styles}
                  onChoose={this.onItemActive}
                  onChange={this.updateSortableList}
                />
              )
            }
          </div>
          <PanelEditProps
            activeItem={activeItem}
            disablePanel={!screen}
            addElement={this.openModalAdd}
            dellElement={this.handleDellElement}
            onChangeMove={this.changeMoveElements}
            onChangeGrid={console.log}
            isMoving={isMoving}
          />
        </div>
        <ModalAddElement
          active={modalAddScreen}
          scenario={scenario}
          changeScenario={this.changeScenario}
          onSelectScenario={this.onSelectScenario}
          onConfirm={this.onConfirmModal}
          onCloseModal={this.closeModal}
        />
        {modalDellScreen && (
          <Modal
            title=""
            confirmText="Delete"
            onConfirm={this.onConfirmModalDell}
            cancelText="Close"
            onClose={this.closeModal}
          >
            <div className={styles.modal__body}>
              {`Delete Screen: ${screen.name}`}
            </div>
          </Modal>
        )}
        <Prompt
          when={false}
          message="You have unsaved changes, are you sure you want to leave?"
        />
      </div>
    );
  }
}

export default Editor;

